.sidebar {
  color: #fff;
  width: 200px;
  background-color: #120444;
  text-align: center;
  &__page {
    &_on {
      transition: all 0.2s;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      transform: translateX(0);
    }
    &_off {
      transition: all 0.2s;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      transform: translateX(-100%);
    }
  }
  &__title {
    font-weight: bold;
    font-size: 30px;
    display: flex;
    padding: 20px 0 10px;
    justify-content: center;
  }
  & a {
    color: #fff;
    &:hover {
      color: #fff;
      text-decoration: none;
    }
  }
}
