.todo {
  &__bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 25px;
    margin-bottom: 20px;
    &_completed {
      margin-top: 30px;
    }
    &_title {
      font-weight: bold;
      color: #333;
    }
    &_number {
      color: #68b4b5;
    }
    & button {
      margin-left: 5px;
    }
    &_layout {
      display: flex;
      flex-direction: row;
    }
  }
}
