.header {
  background-color: #fff;
  padding: 15px 0;
  border-bottom: 1px solid #d8d8d8;
  &__button {
    font-size: 30px;
    cursor: pointer;
    outline: none;
  }
  &__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1400px;
    margin: 0 auto;
  }
  &__right {
    display: flex;
    flex-direction: row;
    align-items: center;
    &_image {
      height: 20px;
      width: 20px;
    }
    &_item {
      margin-left: 10px;
      padding-left: 10px;
    }
    &_link {
      background-color: #7b7bff;
      padding: 7px 15px;
      border-radius: 20px;
      color: #fff;
      &:hover {
        color: #fff;
        text-decoration: none;
      }
    }
    &_button {
      font-size: 30px;
      cursor: pointer;
      padding: 5px;
    }
  }
}
