.header {
  &__dropdown {
    position: relative;
    display: inline-block;
    &:hover {
      & .header__dropdown_list {
        display: block;
      }
      & .header__dropdown_image {
        background-color: #3e8e41;
      }
    }
    &_image {
      height: 35px;
      width: 35px;
      border-radius: 50%;
      border: none;
      cursor: pointer;
    }
    &_list {
      right: 0;
      display: none;
      position: absolute;
      background-color: #f9f9f9;
      min-width: 160px;
      box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
      z-index: 1;
    }
    &_item {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      &:hover {
        background-color: #f1f1f1;
        color: #000;
        text-decoration: none;
      }
    }
  }
}
