.sidebar {
  &__list {
    &_item {
      padding: 20px;
      display: flex;
      justify-content: flex-start;
      &-icon {
        margin-right: 10px;
        color: #6562ef;
      }
    }
  }
  &__link {
    display: flex;
  }
  &__active {
    border-left: 4px solid #5c59dc;
    background-color: #252377;
  }
}
