.page {
  transition: padding 0.2s;
  position: relative;
  z-index: 0;
  padding: 0;
  &__inner {
    margin: 0;
    padding: 0 30px;
  }
  &__sidebar {
    &_on {
      transition: all 0.2s;
      padding-left: 200px;
    }
    &_animation {
      transition: all 0.2s;
    }
  }
}

.main {
  max-width: 1400px;
  margin: auto;
  margin-top: 30px;
}
