.notes {
  &__inner {
    border: 1px solid #f5ebeb;
    margin-bottom: 15px;
    padding: 5px 20px;
  }
  &__item {
    &_completed {
      color: #1cc31c;
      font-style: italic;
    }
    &_top {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
    }
    &_content {
      display: flex;
      flex-direction: row;
      word-wrap: break-word;
      &-inner {
        margin: 0 20px;
        width: calc(100% - 130px);
      }
      &-text {
        word-wrap: break-word;
        color: #729895;
      }
    }
    &_date {
      &-wrapper {
        display: flex;
        align-items: center;
        font-size: 13px;
        color: #61dcc2;
      }
      &-icon {
        display: flex;
        align-items: center;
        margin-right: 5px;
      }
    }
    &_priority {
      margin-right: 10px;
      font-size: 13px;
      &-color {
        height: 50px;
        width: 50px;
        border-radius: 50%;
      }
      &-low {
        background-color: #e4dde4;
      }
      &-medium {
        background-color: #da9fb9b5;
      }
      &-hight {
        background-color: #a83859d9;
      }
    }
  }
  &__dropdown {
    position: relative;
    display: inline-block;
    &:hover {
      & .notes__dropdown_list {
        display: block;
      }
    }
    &_icon {
      font-size: 23px;
      border: none;
      cursor: pointer;
      padding-left: 10px;
    }
    &_list {
      right: 0;
      display: none;
      position: absolute;
      background-color: #f9f9f9;
      min-width: 160px;
      box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
      z-index: 1;
    }
    &_item {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      width: 100%;
      text-align: left;
      &:hover {
        background-color: #f1f1f1;
        color: #000;
        text-decoration: none;
      }
    }
  }
}
