* {
  margin: 0;
  padding: 0;
  outline: none !important;
  list-style-type: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  &:hover {
    color: #fff;
    text-decoration: none;
  }
}

button {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}
